<template>
    <div class="container">
        <input v-model="searchTerm" placeholder="Start typing here...">
        <div v-for="n in names"
          :key="n"> 
          <div v-html="matchName(n)"></div>
  </div>
    </div>
</template>
<script>
  export default {
        data() {
    return {
      names: ['John', 'Johan', 'Deon', 'Derek', 'Alex', 'Alejandro'],
      searchTerm: ''
    };
  },
  methods: {
    matchName(current) {
      let reggie = new RegExp(this.searchTerm, "ig");
      let found = current.search(reggie) !== -1;
      return !found ? current : current.replace(reggie, '<span class="red">' + this.searchTerm + '</span>');
    }
  }
  }
</script>
<style>
.red {
  color: red;
}
</style>